export default {
  name: "UserArticle",
  async asyncData({ $axios, route }) {
    // 用户名
    let username = route.path.slice(2);

    // 用户信息
    const { profile } = await $axios.$get(`profiles/${username}`);

    return { username, profile };
  },
  data() {
    return {
      //
      articles: [],
      articlesCount: 0,
      // 分页
      pageLimit: 5,
      pageIndex: 1,
      //
      author: null,
      favorited: null,
      //
      feedLoading: false,
    };
  },
  created() {
    this.author = this.$route.path.slice(2);
    this.handleGetUserArticle();
  },
  methods: {
    // 获取用户文章
    handleGetUserArticle() {
      this.feedLoading = true;
      this.articles = [];
      this.articlesCount = 0;
      //
      this.$axios
        .$get("articles", {
          params: {
            limit: this.pageLimit,
            offset: (this.pageIndex - 1) * this.pageLimit,
            author: this.author,
            favorited: null,
          },
        })
        .then(({ articles, articlesCount }) => {
          this.articles = articles;
          this.articlesCount = articlesCount;
        })
        .finally(() => {
          this.feedLoading = false;
        });
    },
    // 选择页码
    handleCheckPage(index) {
      this.pageIndex = index;
      this.handleGetUserArticle();
    },
  },
};
